import Vue from 'vue'
import Vuex from 'vuex'

import $H from '../common/request.js'
import $C from '../common/config.js'
import io from '../common/uni-socket-io.js'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		user: null,
		token: null,
		socket: null
	},
	actions: {
		// socket
		connectSocket({
			state,
			dispatch
		}) {
			const S = io($C.socketUrl, {
				query: {},
				transports: ['websocket'],
				timeout: 5000
			})
			//
			let onlineEvent = (e) => {
				uni.$emit('live', {
					type: "online",
					data: e
				})
			}
			//
			let commentEvent = (e) => {
				uni.$emit('live', {
					type: "comment",
					data: e
				})
			}
			//
			let giftEvent = (e) => {
				uni.$emit('live', {
					type: "gift",
					data: e
				})
			}

			// 
			S.on('connect', () => {
				console.log('socket')
				// //
				// S.emit('test', 'socket')
				// //
				// S.on(S.id, (e) => {
				// 	console.log(e);
				// })
				state.socket = S
				// socket.ioidid
				const {
					id
				} = S
				S.on(id, (e) => {
					let d = e.data
					if (d.action === 'error') {
						let msg = d.payload
						if (e.meta.notoast) {
							return
						}
						return uni.showToast({
							title: msg,
							icon: 'none'
						});
					}
				})
				//
				S.on('online', onlineEvent)
				//
				S.on('comment', commentEvent)
				//
				S.on('gift', giftEvent)
			})
			// 
			const removeListener = () => {
				if (S) {
					S.removeListener('online', onlineEvent)
					S.removeListener('comment', commentEvent)
					S.removeListener('gift', giftEvent)
				}
			}
			// 
			S.on('error', () => {
				removeListener()
				state.socket = null
				console.log('socket')
			})
			// 
			S.on('disconnect', () => {
				removeListener()
				state.socket = null
				console.log('')
			})
		},

		// Vuex
		authMethod({
			state
		}, callback) {
			if (!state.token) {
				uni.showToast({
					title: '',
					icon: 'none'
				});
				return uni.navigateTo({
					url: '/pages/login/login'
				});
			}
			callback()
		},
		//
		initUser({
			state
		}) {
			let u = uni.getStorageSync('user')
			let t = uni.getStorageSync('token')
			if (u) {
				state.user = JSON.parse(u)
				state.token = t
			}
		},
		//
		logout({
			state
		}) {
			$H.post('/logout', {}, {
				token: true,
				toast: false
			})
			state.user = null
			state.token = null
			uni.removeStorageSync('user')
			uni.removeStorageSync('token')
		},
		//
		login({
			state
		}, user) {
			state.user = user
			state.token = user.token

			uni.setStorageSync('user', JSON.stringify(user))
			uni.setStorageSync('token', user.token)
		},
		//
		getUserInfo({
			state
		}) {
			$H.get('/user/info', {
				token: true,
				noJump: true,
				toast: false
			}).then(res => {
				state.user = res
				uni.setStorage({
					key: "user",
					data: JSON.stringify(state.user)
				})
			})
		}

	}
})
